import React, { useState, useEffect } from 'react';
import { Sidebar, Menu, MenuItem, SubMenu } from 'react-pro-sidebar';
import { Link } from 'react-router-dom';
import logo from '../../assets/images/n8Logo.png'
import { Typography } from '@mui/material';
import userLogin from '../../Session/userLogin';
import TMInc from '../TMInc';
import { useNavigate } from 'react-router-dom'

const CustomSidebar = (props) => {

    const [currentTab, setcurrentTab] = useState("")
    const navigate = useNavigate();

    const { UserInfo, userCreds, removeUserSession } = userLogin()
    const onclickMenu = (tabItem) => {
        console.log(tabItem)
        if (currentTab == tabItem) {
            setcurrentTab("")
        } else {
            setcurrentTab(tabItem)
        }
    }

    const LogOut = () => {
        removeUserSession()
        props.setLoggedIn(false)
        navigate('/')
    }

    console.log(userCreds)

    return (
        <div>
            <div style={{ display: 'flex', justifyContent: 'center', paddingTop: "10%", }}>
                <img src={logo} alt="N8 Logo" style={{ width: "150px", height: "150px", borderRadius: "50%", border: '2px solid black' }} />
            </div>
            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center', marginTop: '5%' }}>
                <Typography>
                    {`${UserInfo.FirstName} ${UserInfo.MiddleName}. ${UserInfo.LastName}`}
                </Typography>
            </div>

            {userCreds?.creds == "admin" ?

                <Sidebar style={{ width: "100%", paddingTop: "5%" }}>
                    <Menu
                        menuItemStyles={{
                            button: {
                                // the active class will be added automatically by react router
                                // so we can use it to style the active menu item
                                [`&.active`]: {
                                    backgroundColor: '#13395e',
                                    color: '#b6c8d9',
                                },
                            },
                        }}
                    >
                        <SubMenu label="Account Management" onClick={() => onclickMenu("Inventory")} open={true}>
                            <MenuItem component={<Link to="/AdminPage" />}> Account Enrollment</MenuItem>
                            <MenuItem component={<Link to="/accountRecovery" />}> Account Recovery</MenuItem>
                            <MenuItem component={<Link to="/e-commerce" />}> Account Inquiry</MenuItem>
                        </SubMenu>

                        <SubMenu label="Admin Account Maintenance" onClick={() => onclickMenu("Maintenance")} open={true}>
                            <MenuItem component={<Link to="/" />}> Profile </MenuItem>
                            <MenuItem component={<Link to="/Recover" />}> Password Change</MenuItem>
                        </SubMenu>


                    </Menu>

                    <div style={{ width: '-moz-available', marginTop: '100px', display: 'flex', justifyContent: 'center' }}>
                        <TMInc.Button label="LOGOUT" variant='contained' onClick={() => { LogOut() }} />
                    </div>


                </Sidebar>

                :

                <Sidebar style={{ width: "100%", paddingTop: "5%" }}>
                    <Menu
                        menuItemStyles={{
                            button: {
                                // the active class will be added automatically by react router
                                // so we can use it to style the active menu item
                                [`&.active`]: {
                                    backgroundColor: '#13395e',
                                    color: '#b6c8d9',
                                },
                            },
                        }}
                    >

                        <MenuItem component={<Link to="/StudentPage" />}> Profile </MenuItem>
                        <MenuItem component={<Link to="/subjects" />}> Subjects </MenuItem>
                        <MenuItem component={<Link to="/performance" />}> Performance</MenuItem>

                    </Menu>

                    <div style={{ width: '-moz-available', marginTop: '100px', display: 'flex', justifyContent: 'center' }}>
                        <TMInc.Button label="LOGOUT" variant='contained' onClick={() => { LogOut() }} />
                    </div>

                </Sidebar>}

        </div>

    )
}

export default CustomSidebar