import React from 'react'
import * as APIHelper from '../utils/Axios/APIHelper'
import { create } from 'zustand';

const userLogin = create((set) => ({
    accountInfo: null,
    accountInfoError: null,

    loginUserError: null,
    loginData: null,

    UserInfo: null,
    setUserInfo: (data) => set({ UserInfo: data }),

    userCreds: null,
    setuserCreds: (data) => set({ userCreds: data }),


    setloginData: (data) => set({ loginData: data }),
    // categoryPostResponse: null,
    // postCategoryError: null,

    removeUserSession: async () => {
        set({ loginData: null });
        set({ loginUserError: null });

        set({ accountInfo: null });
        set({ accountInfoError: null });
    },

    loginUser: async (reqData, rerender = false) => {

        console.log(reqData)

        if (rerender == true) {
            set({ loginData: null });
            set({ loginUserError: null });
        } else {
            try {
                const response = await APIHelper.GET("/api/loginUser" + reqData)
                console.log(response)
                if (response != undefined) {
                    if (response.data != undefined) {
                        console.log(response)
                        set({ loginData: response.data });
                    }
                }

            } catch (error) {
                console.log(error)
                set({ loginUserError: error });

            }
        }
    },

    getAccountInfo: async (reqData, rerender = false) => {

        console.log(reqData)

        if (rerender == true) {
            set({ accountInfo: null });
            set({ accountInfoError: null });
        } else {
            try {
                const response = await APIHelper.GET("/api/getAccountInfo" + reqData)
                console.log(response)
                if (response != undefined) {
                    if (response.data != undefined) {
                        console.log(response)
                        set({ accountInfo: response.data });
                    }
                }

            } catch (error) {
                console.log(error)
                set({ accountInfoError: error });
            }
        }
    },



}));

export default userLogin