import userLogin from '../Session/userLogin';

const MainContainer = (props) => {
    const navigate = useNavigate();
    const { userCreds } = userLogin()

    useEffect(() => {
        if (userCreds == null) {
            //navigate('/')
        }
    }, [userCreds])

    return (
        <>
            {props.children}
        </>
    )
}

export default MainContainer