import { Grid, Typography } from '@mui/material'
import React, { useState, useEffect } from 'react';
import logo from '../../assets/images/TMIncLogo.jpg'
import TMInc from '../../components/TMInc'
import styles from './LoginPageStyle'
import { useNavigate } from 'react-router-dom'
import { Link } from 'react-router-dom';
import PasswordRecover from '../PasswordRecover/PasswordRecover'
import NewPassword from '../NewPassword/NewPassword';
import userLogin from '../../Session/userLogin';
import SignUp from './SignUp';

const LoginPage = (props) => {
    const classes = styles;
    const navigate = useNavigate();
    const { loginUser, loginData, getAccountInfo, accountInfo, setUserInfo, setuserCreds, userCreds } = userLogin()
    const [recoverScreen, setrecoverScreen] = useState(false)
    const [loginScreen, setloginScreen] = useState(true)
    const [changePassScreen, setchangePassScreen] = useState(false)

    const [currentPage, setcurrentPage] = useState('Login')
    const [email, setemail] = useState('')
    const [pass, setpass] = useState('')


    const LoginOnCLick = () => {
        //navigate("/Category")
        if (email.length == 0 || pass.length == 0) {
            alert("Incomplete Credentials")
        } else {
            props.setloading(true)
            loginUser(`?email=${email}&pass=${pass}`)
        }
        //props.removeNav()
    }

    useEffect(() => {
        if (loginData != null || loginData != undefined) {
            if (loginData.length == 1) {
                //setloginData(loginData[0])
                //navigate("/Dashboard")
                console.log(loginData[0])
                setuserCreds(loginData[0])
                getAccountInfo(`?id=${loginData[0].StudentID}`)
            } else {
                alert("Account Does Not Exist")
                loginUser("", true)
                props.setloading(false)
            }
        }
    }, [loginData])

    useEffect(() => {
        if (accountInfo != null || accountInfo != undefined) {
            if (accountInfo.length == 1) {
                //setloginData(loginData[0])
                //navigate("/Dashboard")
                console.log(accountInfo[0])
                setUserInfo(accountInfo[0])
                props.setLoggedIn(true)
                props.setloading(false)
                navigate(userCreds.creds == "admin" ? '/AdminPage' : '/StudentPage')
            } else {
                alert("Account Does Not Exist")
                getAccountInfo("", true)
                props.setloading(false)
            }
        }
    }, [accountInfo])

    const RecoverOnCLick = () => {
        setrecoverScreen(true)
        setloginScreen(false)
        setchangePassScreen(false)
    }

    const AuthPass = () => {
        setrecoverScreen(false)
        setloginScreen(false)
        setchangePassScreen(true)
    }

    const SignUpData = {
        currentPage,
        setcurrentPage,
    }

    return (
        <Grid container style={{ backgroundColor: 'white', display: 'flex', justifyContent: 'center', height: '100%', flexDirection: 'column', boxShadow: '30px 30px 0px #0170ff' }}>
            <Grid item style={{ display: 'flex', justifyContent: 'center', height: "20%", alignItems: 'center' }}>
                <img src={logo} alt="TMIncLogo" style={{ width: '30%', height: 'fit-content' }} />
            </Grid>

            {currentPage == "Login" ?
                <Grid item style={{ display: 'flex', justifyContent: 'center', height: "60%" }}>
                    <Grid container direction="row">
                        <Grid item xs={12} justifyContent="center" >
                            <Typography variant='h4' style={{ textAlign: "center", color: '#0170ff', fontWeight: 'bold' }}>
                                Login
                            </Typography>
                            <Typography variant='h6' style={{ textAlign: "center" }}>
                                Sign in to your account
                            </Typography>
                        </Grid>

                        <Grid item marginTop="30%" style={{ display: 'flex', justifyContent: 'center' }}>
                            <Grid container direction="row" width="80%">
                                <Grid item xs={12}>
                                    <TMInc.TextField label="Email" fullWidth setValue={setemail} />
                                </Grid>
                                <Grid item xs={12} marginTop='10px'>
                                    <TMInc.TextField label="Password" fullWidth setValue={setpass} />
                                </Grid>

                                <Grid item xs={12} marginTop='20px' direction="column" style={{ display: 'flex', justifyContent: 'center' }}>
                                    <TMInc.Button label="Login"
                                        variant="contained"
                                        style={{ width: '-moz-available', fontSize: '15px', fontWeight: 'bold', backgroundColor: '#0170ff', paddingTop: '10px', paddingBottom: '10px' }}
                                        onClick={LoginOnCLick} />
                                </Grid>
                                <Grid item xs={12} marginTop='10px'>
                                    <div style={{ display: 'flex', justifyContent: 'center' }}>
                                        <Link underline="hover" style={{ marginLeft: "8px", marginRight: '8px' }} > I forgot my password click here to reset</Link>
                                    </div>
                                </Grid>

                            </Grid>
                        </Grid>
                    </Grid>

                </Grid>

                : currentPage == "SignUp" ?
                    <SignUp props={SignUpData} />
                    :
                    null
            }


            <Grid item style={{ display: 'flex', justifyContent: 'center', height: "10%", alignItems: 'center' }}>
                {/* <Typography style={{ textAlign: "center" }}> */}
                {currentPage == "Login" ?
                    <>
                        <div style={{ width: '80%' }}>
                            <TMInc.Button label="Register New Account"
                                variant='outlined'
                                style={{ width: '-moz-available', fontSize: '15px', fontWeight: 'bold', backgroundColor: 'white', font: 'black', paddingTop: '10px', paddingBottom: '10px' }}
                                onClick={() => setcurrentPage('SignUp')} />

                            {/* Don't have an account? <Link underline="hover" onClick={() => setcurrentPage('SignUp')}>Sign-up</Link> */}
                        </div>



                    </>
                    : null}
                {/* </Typography> */}
            </Grid>
        </Grid >

    )
}

export default LoginPage
