import React from 'react'
import LocationOnIcon from '@mui/icons-material/LocationOn';
import CallIcon from '@mui/icons-material/Call';
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import InstagramIcon from '@mui/icons-material/Instagram';
import TMInc from '../../components/TMInc'
import { Typography } from '@mui/material';
import logo from '../../assets/images/TMIncLogo.jpg'
import { useNavigate } from 'react-router-dom'

const Header = () => {
    const navigate = useNavigate();

    const openTab = (url) => {
        var win = window.open(url, '_blank');
        win.focus();
    }

    const navigateToLogin = () => {
        navigate('/Login')
    }

    return (
        <div style={{ width: 'auto', justifyContent: 'center' }}>
            <div style={{ display: 'flex', alignItems: 'center', height: '5%', backgroundColor: '#e0dcdc', width: '100vw', justifyContent: 'space-around', padding: '5px' }}>
                <Typography
                    style={{
                        display: 'flex',
                        alignItems: 'center',
                        width: 'fit-content',
                        textAlign: 'center',
                    }}

                    variant='h5'
                >
                    <LocationOnIcon /> Lambakin, Marilao, Bulacan | &nbsp; <CallIcon />(044) 711-1092
                </Typography>
                <div style={{ width: '20vw' }}></div>
                <Typography
                    style={{
                        display: 'flex',
                        alignItems: 'space-around',
                        width: 'fit-content',
                        textAlign: 'center',
                    }}

                    variant='h5'
                >
                    <FacebookIcon fontSize='large' onClick={() => openTab("https://www.facebook.com/TMCSOfficialpage")} /> &nbsp; &nbsp; <TwitterIcon fontSize='large' /> &nbsp; &nbsp; <InstagramIcon fontSize='large' />
                </Typography>
            </div>

            <div style={{ width: '100vw', display: 'flex', justifyContent: 'center' }}>
                <div style={{ display: 'flex', alignItems: 'center', height: '5%', width: '80vw', justifyContent: 'space-around', padding: '5px' }}>
                    <img src={logo} alt="TMIncLogo" style={{ width: '10%', height: 'fit-content' }} />
                    <div style={{ display: 'flex', flexDirection: 'row' }}>
                        <Typography
                            style={{
                                display: 'flex',
                                alignItems: 'space-around',
                                width: 'fit-content',
                                textAlign: 'center',
                            }}

                            variant='h5'
                        >
                            <TMInc.Button label="HOME" style={{ fontSize: "25px", paddingLeft: '20px', paddingRight: '20px', color: 'black' }} />


                        </Typography>

                        <Typography
                            style={{
                                display: 'flex',
                                alignItems: 'space-around',
                                width: 'fit-content',
                                textAlign: 'center',
                            }}

                            variant='h5'
                        >
                            <TMInc.Button label="ABOUT" style={{ fontSize: "25px", paddingLeft: '20px', paddingRight: '20px', color: 'black' }} />

                        </Typography>

                        <Typography
                            style={{
                                display: 'flex',
                                alignItems: 'space-around',
                                width: 'fit-content',
                                textAlign: 'center',
                            }}

                            variant='h5'
                        >
                            <TMInc.Button label="EVENT" style={{ fontSize: "25px", paddingLeft: '20px', paddingRight: '20px', color: 'black' }} />
                        </Typography>

                        <Typography
                            style={{
                                display: 'flex',
                                alignItems: 'space-around',
                                width: 'fit-content',
                                textAlign: 'center',
                            }}

                            variant='h5'
                        >
                            <TMInc.Button label="COURSES" style={{ fontSize: "25px", paddingLeft: '20px', paddingRight: '20px', color: 'black' }} />

                        </Typography>

                        <Typography
                            style={{
                                display: 'flex',
                                alignItems: 'space-around',
                                width: 'fit-content',
                                textAlign: 'center',
                            }}

                            variant='h5'
                        >
                            <TMInc.Button label="EDUCATIONAL PROGRAM" style={{ fontSize: "25px", paddingLeft: '20px', paddingRight: '20px', color: 'black' }} />
                        </Typography>
                    </div>

                    <TMInc.Button label="LOGIN" variant="outlined" onClick={navigateToLogin} style={{ fontSize: "20px", borderRadius: '30px', paddingLeft: "3%", paddingRight: "3%", paddingTop: '5px', paddingBottom: '5px' }} />
                </div>
            </div>

        </div>

    )
}

export default Header