import { Grid, Typography } from '@mui/material'
import React from 'react'
import TMInc from '../../components/TMInc'
import { Link } from 'react-router-dom'

const SignUp = ({ props }) => {
    console.log(props)
    return (
        <Grid item style={{ display: 'flex', justifyContent: 'center', height: "60%" }}>
            <Grid container direction="row">
                <Grid item justifyContent="center" xs={12}>
                    <Typography variant='h4' style={{ textAlign: "center", color: '#0170ff' }}>
                        Register
                    </Typography>
                </Grid>

                <Grid item marginTop="10%" xs={12} style={{ display: 'flex', justifyContent: 'center' }}>
                    <Grid container direction="row" width="80%">
                        <Grid item xs={12} marginTop='10px'>
                            <TMInc.TextField label="Student/Employee ID" fullWidth />
                        </Grid>
                        <Grid item xs={12} marginTop='10px'>
                            <TMInc.TextField label="Email" fullWidth />
                        </Grid>
                        <Grid item xs={12} marginTop='10px'>
                            <TMInc.TextField label="Password" fullWidth />
                        </Grid>
                        <Grid item xs={12}>
                            <TMInc.TextField label="First Name" fullWidth />
                        </Grid>
                        <Grid item xs={12} marginTop='10px'>
                            <TMInc.TextField label="Middle Name" fullWidth />
                        </Grid>
                        <Grid item xs={12} marginTop='10px'>
                            <TMInc.TextField label="Last Name" fullWidth />
                        </Grid>
                        {/* <Grid item xs={12} marginTop='10px'>
                            <div style={{ display: 'flex', justifyContent: 'end' }}>
                                <Link underline="hover" >Forgot Password?</Link>
                            </div>
                        </Grid> */}
                        <Grid item xs={12} marginTop='30px' style={{ display: 'flex', justifyContent: 'space-around' }}>
                            <TMInc.Button label="Back" variant="contained" style={{ width: '120px', fontSize: '15px', backgroundColor: '#0170ff' }} onClick={() => props.setcurrentPage("Login")} />
                            <TMInc.Button label="Submit" variant="contained" style={{ width: '120px', fontSize: '15px', backgroundColor: '#0170ff' }} />
                        </Grid>

                    </Grid>
                </Grid>
            </Grid>

        </Grid>
    )
}

export default SignUp