import './App.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'
import React, { useState, useEffect } from 'react';
import LoginPage from './pages/LoginPage/LoginPage';
import PasswordRecover from './pages/PasswordRecover/PasswordRecover';
import CustomSidebar from './components/sidebar/sidebar';
import { Grid, Typography } from '@mui/material';
import MainContainer from './pages/MainContainer';
import userLogin from './Session/userLogin';
import TMInc from './components/TMInc';
import logo from '../src/assets/images/TMIncLogo.jpg'
import logoClear from '../src/assets/images/TMIncClearBG.png'
import BG from '../src/assets/images/tempBG.jpg'

import AccountEnrollment from './pages/AdminPages/AccountEnrollment/AccountEnrollment'
import Profile from './pages/StudentPages/Profile/Profile';
import LoadingScreen from 'react-loading-screen';
import Homepage from './pages/Homepage/Homepage';
// import Category from './pages/Category/Category';
// import DashBoard from './pages/DashBoard/DashBoard';
// import Brands from './pages/Brands/Brands';

function App() {
  const [loggedIn, setLoggedIn] = useState(false)
  const { UserInfo, userCreds, removeUserSession } = userLogin()


  const [loading, setloading] = useState(false)
  // console.log(loggedIn)

  const LogOut = () => {
    removeUserSession()
    // navigate('/')
  }

  const AppData = {
    setLoggedIn,
    setloading,
  }

  return (
    <Router>

      <MainContainer >
        <LoadingScreen
          loading={loading}
          bgColor='rgba(255, 255, 255, 0.5)'
          spinnerColor='#0170ff'
          logoSrc={logoClear}
        />

        <Grid container style={{ height: '100vh' }} spacing={0}>

          {window?.location?.pathname != "/Login" && window?.location?.pathname != "/" ?

            <React.Fragment>
              <Grid item xs={3} >
                < CustomSidebar {...AppData} />
              </Grid>
            </React.Fragment>

            : <></>}

          <Routes>
            <Route path="/Login" element={
              <>
                {loggedIn == true ?
                  <Grid item xs={3} >
                    < CustomSidebar {...AppData} />
                  </Grid>
                  :
                  null
                }

                {loggedIn == true ?
                  <Grid item xs={9} style={{ backgroundColor: 'gray' }}>
                    <Grid container direction="row">
                      <Grid item xs={12} style={{ height: '15vh' }}>
                        {userCreds?.creds == 'admin' ?
                          <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'center', height: '100%' }}>
                            <TMInc.Button label="Logout" variant="contained" style={{ padding: '15px', width: '150px', marginRight: '15px' }} />
                          </div>
                          :
                          <>
                            <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'center', height: '100%' }}>
                              <img src={logo} alt="TMIncLogo" style={{ width: '80px', height: '80px', marginRight: '15px' }} />
                            </div>
                          </>
                        }
                      </Grid>
                      <Grid item xs={12} style={{ backgroundColor: 'white', height: '85vh', width: '100%' }}>
                        <Grid container direction='row' style={{ height: "100%" }} >
                          <Grid item style={{ backgroundColor: 'green', display: 'contents', height: "100%" }}>

                            {userCreds?.creds == "admin" ?

                              <Routes>
                                <Route path="/AdminPage" element={<AccountEnrollment />} />
                              </Routes>
                              :
                              <Routes>
                                <Route path="/StudentPage" element={<Profile />} />
                              </Routes>

                            }
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                  </Grid>
                  :
                  <Grid item xs={8} style={{ height: '100', width: '100%' }}>
                    <div style={{ height: '100', width: '100%', display: 'flex' }}>
                      <div style={{ backgroundColor: 'rgba(37, 125, 190, 0.9)', width: '-moz-available', height: '100%', position: 'absolute', top: '0px' }}>
                        <div style={{ display: 'flex', height: '100%', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
                        </div>
                      </div>
                      <img src={BG} alt="TMIncLogo" style={{ width: '100%', height: '100%' }} />
                    </div>
                  </Grid>
                }

                {loggedIn == true ?
                  null
                  :
                  <Grid item xs={4} style={{ zIndex: '1', backgroundColor: '#f2f2f2', padding: '5%' }}>
                    <Routes>
                      <Route path="/" element={<LoginPage {...AppData} />} />
                    </Routes>
                  </Grid>
                }
              </>
            } />
            <Route path="/" element={<Homepage />} />




            <Route path="/AdminPage" element={
              <Grid item xs={9} style={{ backgroundColor: 'gray' }}>
                <Grid container direction="row">
                  <Grid item xs={12} style={{ height: '15vh' }}>
                    {userCreds?.creds == 'admin' ?
                      <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'center', height: '100%' }}>
                        {/* <TMInc.Button label="Logout" variant="contained" style={{ padding: '15px', width: '150px', marginRight: '15px' }} /> */}
                        <img src={logo} alt="TMIncLogo" style={{ width: '80px', height: '80px', marginRight: '15px' }} />
                      </div>
                      :
                      <>
                        <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'center', height: '100%' }}>
                          <img src={logo} alt="TMIncLogo" style={{ width: '80px', height: '80px', marginRight: '15px' }} />
                        </div>
                      </>
                    }
                  </Grid>
                  <Grid item xs={12} style={{ backgroundColor: 'white', height: '85vh', width: '100%' }}>
                    <Grid container direction='row' style={{ height: "100%" }} >
                      <Grid item style={{ backgroundColor: 'green', display: 'contents', height: "100%" }}>

                        <AccountEnrollment />

                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            } />






            <Route path="/StudentPage" element={
              <Grid item xs={9} style={{ backgroundColor: 'gray' }}>
                <Grid container direction="row">
                  <Grid item xs={12} style={{ height: '15vh' }}>
                    {userCreds?.creds == 'admin' ?
                      <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'center', height: '100%' }}>
                        {/* <TMInc.Button label="Logout" variant="contained" style={{ padding: '15px', width: '150px', marginRight: '15px' }} /> */}
                        <img src={logo} alt="TMIncLogo" style={{ width: '80px', height: '80px', marginRight: '15px' }} />

                      </div>
                      :
                      <>
                        <div style={{ display: 'flex', justifyContent: 'end', alignItems: 'center', height: '100%' }}>
                          <img src={logo} alt="TMIncLogo" style={{ width: '80px', height: '80px', marginRight: '15px' }} />
                        </div>
                      </>
                    }
                  </Grid>
                  <Grid item xs={12} style={{ backgroundColor: 'white', height: '85vh', width: '100%' }}>
                    <Grid container direction='row' style={{ height: "100%" }} >
                      <Grid item style={{ backgroundColor: 'green', display: 'contents', height: "100%" }}>

                        <Profile />

                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            } />



          </Routes>







        </Grid>

      </MainContainer>

    </Router >
  );


}

export default App;
