import React from 'react'
import Header from './Header'
import { Carousel } from 'react-responsive-carousel';
import SlidePic1 from '../../assets/images/SlidePic1.jpg'
import SlidePic2 from '../../assets/images/SlidePic2.jpg'
import SlidePic3 from '../../assets/images/SlidePic3.jpg'
import Philosophy from '../../assets/images/Philosophy.jpg'
import banner from '../../assets/images/banner.png'
import { width } from '@mui/system';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Grid, Typography } from '@mui/material';
import TMInc from '../../components/TMInc';
import Facebook from '@mui/icons-material/Facebook';
import Twitter from '@mui/icons-material/Twitter';
import Instagram from '@mui/icons-material/Instagram';

const Homepage = () => {

    const images = [SlidePic1, SlidePic2, SlidePic3]

    const cards = [
        {
            image: SlidePic1,
            text: `The Cebuana host and entrepreneur bested 43 other aspirants to inherit the crown from last year\’s winner Valentina Sanchez, who also happens to be from Venezuela. Gigante\’s victory has essentially thwarted the South American country\’s attempt at scoring back-to-back victories in the young competition.`
        },

        {
            image: SlidePic2,
            text: `Gigante also received the Universal Woman Sympathy title during the preliminary competition, and her social project was among the Top 5 chosen by the selection committee. She amassed the most votes in the online poll that earned for her the Universal Woman Social Media award during the final show.`
        },

        {
            image: SlidePic3,
            text: `According to reports, Cebu Provincial Governor Gwen Garcia made this clarification after photos of a deforested mountain in Balamban, initially thought to be the site development of the new Capitol building, stirred mostly negative reactions from netizens.However, she noted that the sites cleared`
        },]


    const featured = [SlidePic2, banner, SlidePic1]

    const feedbacks = [SlidePic1, SlidePic2, SlidePic3]

    return (
        <div>
            <Header />

            <div style={{ width: '100%', display: 'flex', justifyContent: 'center', marginBottom: '20px' }}>
                <div >
                    <Carousel
                        autoPlay={true}
                        infiniteLoop={true}
                        showThumbs={false}
                        interval={5000}
                        stopOnHover={false}
                        showArrows={false}
                    >
                        {images.map((imgs) => {
                            return (
                                <div>
                                    <img src={imgs} style={{ height: '600px' }} />
                                </div>
                            )
                        })}
                        {/*                         
                        <div>
                            <img src={SlidePic2} style={{ height: '600px' }} />
                        </div>
                        <div>
                            <img src={SlidePic3} style={{ height: '600px' }} />
                        </div> */}
                    </Carousel>
                </div>
            </div>

            <div style={{ width: '100%', marginBottom: '20px', display: 'flex', justifyContent: 'center' }}>
                <div style={{ width: '90%' }}>
                    <div style={{ width: "100%" }}>
                        <Typography variant='h4' style={{ fontWeight: 'bold', marginBottom: '50px' }} > LATEST EVENTS</Typography>
                    </div>
                    <div style={{ width: "100%", display: 'flex', justifyContent: 'center' }}>
                        <div style={{ width: "90%", display: 'flex', flexDirection: "row", justifyContent: 'space-around' }}>
                            {cards.map((imgs) => {
                                return (
                                    <div style={{ width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                        <img src={imgs.image} style={{ width: '80%', height: '400px', borderRadius: '30px', position: 'relative', zIndex: '1' }} />
                                        <div style={{
                                            width: '76%',
                                            // height: 'max-content',
                                            height: '150px',
                                            overflow: 'hidden', borderRadius: '30px', backgroundColor: 'gray', position: 'relative', marginTop: '-10%', padding: '2%', paddingTop: '10%'
                                        }} >
                                            {imgs.text}
                                        </div>
                                    </div>
                                )
                            })}

                        </div>
                    </div>
                    <div style={{ width: "100%", display: 'flex', justifyContent: 'center', marginTop: '50px' }}>
                        <div style={{ width: "90%", display: 'flex', flexDirection: "row", justifyContent: 'end' }}>
                            <TMInc.Button label="More Event" variant="contained" style={{ padding: '20px', paddingLeft: "30px", paddingRight: "30px", fontWeight: 'bold', borderRadius: '10px' }} />
                        </div>
                    </div>
                </div>
            </div>

            <div style={{ width: '100%', marginBottom: '20px', display: 'flex', justifyContent: 'center' }}>
                <div style={{ width: '90%' }}>
                    <div style={{ width: "100%" }}>
                        <Typography variant='h4' style={{ fontWeight: 'bold', marginBottom: '50px' }} > OUR SCHOOOL PHILOSOPHY</Typography>
                    </div>
                    <div>
                        <Grid container>
                            <Grid item xs={7}>
                                <Typography variant='h5'>
                                    PSA reported a significant per capita growth in almost all areas of the Philippines in 2022. Aklan showed the fastest capita GDP growth of 21.5 percent.
                                    <br />
                                    <br />
                                    Meanwhile, other provinces and HUCs followed suit to the Summer Capital of the Philippines. Cebu City, Davao City, Mandaue City, and the provinces of Laguna and Batanes had above-national average per capita GDPs. Below is the official list of the richest cities outside Metro Manila.
                                </Typography>
                                <TMInc.Button label="READ MORE" variant="outlined" style={{ fontSize: '20px', marginTop: '20px', borderRadius: '10px' }} />
                            </Grid>

                            <Grid item xs={5}>
                                <img src={Philosophy} style={{ width: '-moz-available' }} />
                            </Grid>
                        </Grid>
                    </div>
                </div>
            </div>

            <div style={{ width: '100%', marginBottom: '20px', display: 'flex', justifyContent: 'center' }}>
                <div style={{ width: '90%' }}>
                    <div style={{ width: "100%" }}>
                        <Typography variant='h4' style={{ fontWeight: 'bold', marginBottom: '50px' }} > FEATURED</Typography>
                    </div>
                    <div style={{ width: "100%", display: 'flex', justifyContent: 'center' }}>
                        <div style={{ width: "90%", display: 'flex', flexDirection: "row", justifyContent: 'space-around' }}>

                            {featured.map((imgs) => {
                                return (
                                    <div style={{ width: '100%', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                        <img src={imgs} style={{ width: '80%', height: 'auto', borderRadius: '30px', position: 'relative', zIndex: '1' }} />
                                    </div>
                                )
                            })}

                        </div>
                    </div>

                </div>

            </div>

            <div style={{ width: '100%', marginBottom: '20px', display: 'flex', justifyContent: 'center', backgroundColor: '#0170ff' }}>
                <div style={{ width: '90%' }}>
                    <div style={{ width: "100%", display: 'flex', justifyContent: 'center' }}>
                        <div style={{ width: "100%", display: 'flex', flexDirection: "row", justifyContent: 'space-around', marginTop: '50px' }}>

                            <div style={{ width: 'fit-content', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <div style={{ width: "100%" }}>
                                    <Typography variant='h5' style={{ marginBottom: '50px' }} > STAY CONNECTED</Typography>

                                    <Typography variant='h5' style={{ display: 'flex', alignItems: 'center' }} > <Facebook fontSize='large' /> <a href='https://www.facebook.com/TMCSOfficialpage' target='_blank'>https://www.facebook.com/TMCSOfficialpage</a></Typography>
                                    <Typography variant='h5' style={{ display: 'flex', alignItems: 'center' }} > <Twitter fontSize='large' /> <a href='https://www.facebook.com/TMCSOfficialpage' target='_blank'>https://www.facebook.com/TMCSOfficialpage</a></Typography>
                                    <Typography variant='h5' style={{ marginBottom: '50px', display: 'flex', alignItems: 'center' }} > <Instagram fontSize='large' /> <a href='https://www.facebook.com/TMCSOfficialpage' target='_blank'>https://www.facebook.com/TMCSOfficialpage</a></Typography>
                                </div>
                            </div>

                            <div style={{ width: 'fit-content', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <div style={{ width: "100%" }}>
                                    <Typography variant='h5' style={{ marginBottom: '50px' }} > FEEDBACK</Typography>

                                    <div style={{ width: 'fit-content', marginBottom: '50px' }}>
                                        <Carousel
                                            width='300px'
                                            autoPlay={true}
                                            infiniteLoop={true}
                                            showThumbs={false}
                                            interval={5000}
                                            stopOnHover={false}
                                            showArrows={false}
                                            showIndicators={false}
                                            showStatus={false}
                                        >
                                            {feedbacks.map((imgs) => {
                                                return (
                                                    <div style={{ width: 'fit-content' }}>
                                                        <img src={imgs} style={{ width: '300px', height: "200px" }} />
                                                    </div>
                                                )
                                            })}
                                            {/*                         
                        <div>
                            <img src={SlidePic2} style={{ height: '600px' }} />
                        </div>
                        <div>
                            <img src={SlidePic3} style={{ height: '600px' }} />
                        </div> */}
                                        </Carousel>
                                    </div>
                                </div>
                            </div>

                            <div style={{ width: 'fit-content', display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <div style={{ width: "100%" }}>
                                    <Typography variant='h5' style={{ marginBottom: '50px' }} > LOCATION</Typography>

                                    <div style={{ width: 'fit-content' }}>
                                        <img src={SlidePic1} style={{ width: '300px', height: "200px" }} />
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>

                </div>

            </div>
        </div>
    )
}

export default Homepage