import React, { useEffect, useState } from 'react'
import { Grid } from '@mui/material'
import logo from '../../../assets/images/n8Logo.png'
import TMInc from '../../../components/TMInc'
import userLogin from '../../../Session/userLogin'

const Profile = () => {

    const { UserInfo, userCreds } = userLogin()
    const [email, setemail] = useState('')
    const [fName, setfName] = useState('')
    const [mName, setmName] = useState('')
    const [lName, setlName] = useState('')


    useEffect(() => {
        if (UserInfo != null) {
            setemail(UserInfo?.Email)
            setfName(UserInfo?.FirstName)
            setmName(UserInfo?.MiddleName)
            setlName(UserInfo?.LastName)
        }
    }, [])

    return (
        <Grid container direction="row" style={{ display: 'contents' }}>
            <Grid item style={{ width: "100%", height: "100%", display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                <Grid container direction='row' style={{ height: '90%', display: 'flex', justifyContent: 'space-around' }}>
                    <Grid item xs={4} style={{ backgroundColor: 'gray' }}>
                        <div style={{ display: 'flex', height: '25%', justifyContent: 'center', alignItems: 'center' }}>
                            <img src={logo} alt="N8 Logo" style={{ width: "120px", height: "120px", borderRadius: "50%", border: '2px solid black' }} />
                        </div>
                        <div style={{ marginLeft: '5%', marginRight: '5%' }}>
                            <TMInc.TextField fullWidth style={{ backgroundColor: 'white', marginTop: '5%' }} value={email} setValue={setemail} label="First Name" />
                            <TMInc.TextField fullWidth style={{ backgroundColor: 'white', marginTop: '5%' }} value={fName} setValue={setfName} label="Last Name" />
                            <TMInc.TextField fullWidth style={{ backgroundColor: 'white', marginTop: '5%' }} value={mName} setValue={setmName} label="Middle Name" />
                            <TMInc.TextField fullWidth style={{ backgroundColor: 'white', marginTop: '5%' }} value={lName} setValue={setlName} label="Email Name" />

                        </div>
                    </Grid>

                    <Grid item xs={6} style={{ backgroundColor: 'gray' }}>

                    </Grid>
                </Grid>
            </Grid>

        </Grid>
    )
}

export default Profile